<!--      尊敬的 {{username}}  用户，欢迎使用代理商管理系统,您的系统总数为{{total}}套, 当前剩余{{last}}套-->
<!--    </p>-->

<!--  </div>-->
</template>

<script>
import UserApi from '@/api/user.js';
import { delCookie } from '@/utils/base.js';
export default {
  data() {
    return {}
  },
  created() {
    this.login_out()
  },
  methods: {
    login_out() {
      let that = this
      this.$confirm('此操作将退出登录, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          UserApi.loginOut({})
            .then(data => {
              delCookie('isAgentLogin')
              that.$router.push({
                path: '/Login'
              })
            })
            .catch(error => {});
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消退出'
          });
          that.$router.go(-1)
        });
    }
  }
}
</script>

<style></style>



